:root {
  --nx-header-height: 56px;
}

pre.highlight,
.highlight pre {
  max-height: 30rem;
  margin: 1.5rem 0;
  overflow: auto;
  border-radius: var(--tblr-border-radius);
}

.highlight {
  margin: 0;
}
.highlight code > * {
  margin: 0 !important;
  padding: 0 !important;
}
.highlight .c, .highlight .c1 {
  color: #a0aec0;
}
.highlight .nt, .highlight .nc, .highlight .nx {
  color: #ff8383;
}
.highlight .na, .highlight .p {
  color: #ffe484;
}
.highlight .s, .highlight .dl, .highlight .s2 {
  color: #b5f4a5;
}
.highlight .k {
  color: #93ddfd;
}
.highlight .s1, .highlight .mi {
  color: #d9a9ff;
}

.card-sponsor {
  background: var(--tblr-primary-lt) no-repeat center/100% 100%;
  border-color: var(--tblr-primary);
  min-height: 316px;
}

.dropdown-menu-demo {
  display: inline-block;
  width: 100%;
  position: relative;
  top: 0;
  margin-bottom: 1rem !important;
}

.demo-icon-preview {
  position: sticky;
  top: 0;
}
.demo-icon-preview svg,
.demo-icon-preview i {
  width: 15rem;
  height: 15rem;
  font-size: 15rem;
  stroke-width: 1.5;
  margin: 0 auto;
  display: block;
}

.demo-icon-preview-icon pre {
  margin: 0;
  user-select: all;
}

.demo-dividers > p {
  opacity: 0.2;
  user-select: none;
}

.demo-icons-list {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 0 -2px -1px 0;
  list-style: none;
}
.demo-icons-list > * {
  flex: 1 0 4rem;
}

.demo-icons-list-wrap {
  overflow: hidden;
}

.demo-icons-list-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  aspect-ratio: 1;
  text-align: center;
  padding: 0.5rem;
  border-right: var(--tblr-border-width) var(--tblr-border-style) var(--tblr-border-color);
  border-bottom: var(--tblr-border-width) var(--tblr-border-style) var(--tblr-border-color);
  color: inherit;
  cursor: pointer;
}
.demo-icons-list-item .icon {
  width: 1.5rem;
  height: 1.5rem;
  font-size: 1.5rem;
}
.demo-icons-list-item:hover {
  text-decoration: none;
}

